.header {
  height: 5rem;
  flex-shrink: 0;
  background-color: #ffdab9;
}

.content {
  flex-grow: 1;
  padding-inline: 1rem;
  position: relative;
  height: 100%;
}

.menu {
  display: flex;
  justify-content: space-around;
  align-items: center;

  flex-shrink: 0;
  background-color: #f4a460;
  padding-top: 1rem;
  padding-bottom: calc(1rem + env(safe-area-inset-bottom));
}

.appScreenContainer {
  height: 100%;
}

.screen {
  height: 100%;
}

.homeScreen {
  display: flex;
  flex-direction: column;
}
